<template>
	<view class="container">
		<uni-row class="demo-uni-row" :gutter="20">
			<uni-col :span="6">
				<view class="left-box wx-left-box">
					<scroll-view scroll-y="true" class="scroll-group">
						<uni-list>
							<uni-list-item title="所有商品" :class="groupId==''?'active':''" clickable @click="clickGroup(0)"></uni-list-item>
						    <uni-list-item :title="item.name" :class="groupId==item.id?'active':''" clickable @click="clickGroup(item.id)"  v-for="item in goodsGroup" :key="item.id"></uni-list-item>
						</uni-list>
					</scroll-view>
				</view>
			</uni-col>
			<uni-col :span="18">
				<view class="right-box wx-right-box">
					<scroll-view scroll-y="true" class="scroll-goods" @scrolltolower="scrolltolower">
						<view class="item-box" v-for="(item,index) in goodsList" :key="item.id" @click="toDetail(item.id)">
							<view class="l">
								<image :src="item.image" alt="" class="img"></image>
								<view class="info">
									<view class="name line">{{item.title}}</view>
									<view class="des line2">{{item.describe}}</view>
									<view class="price-box"><text class="price">￥{{item.price}}</text>/份</view>
								</view>
							</view>
							<view class="r" @click.stop="addCartGoods(item.id)">
								<image src="../../static/icon/gouwuche.png" class="icon"></image>
							</view>
						</view>
						<uni-load-more status="noMore"></uni-load-more>
					</scroll-view>
				</view>
			</uni-col>
		</uni-row>
	</view>
</template>

<script>
	import {
		mapState,mapMutations,mapActions
	} from 'vuex'
	export default {
		data() {
			return {
				goodsList: [],
				page: 1,
				allPage: 1,
				pageSize: 10
			}
		},
		onLoad(options) {
			
		},
		computed: {
			...mapState(['groupId','goodsGroup','isAuth'])
		},
		methods: {
			...mapMutations(['setGroupId']),
			...mapActions(['addCartGoods']),
			async getGoodsList(data) { //获取商品列表
			    uni.showLoading({
			    	title: '加载中'
			    });
				let response = await this.$api.goods.getGoodsList(data)
				if (response.data.code == 200) {
					uni.hideLoading()
					this.goodsList =this.goodsList.concat(response.data.data.list) 
					this.allPage = response.data.data.allPage
				}
			},
			clickGroup(id){//点击商品分类
			  let groupId = id==0?'':id
			  this.setGroupId(groupId)
			  this.initList()
			},
			initList(){
				this.goodsList = []
				this.page= 1
				this.allPage= 1
				let data = {
				  groupId:this.groupId,
				  page: this.page,
				  pageSize: this.pageSize
				}
				this.getGoodsList(data)
			},
			toDetail(id){ //跳转商品详情
				uni.navigateTo({
					url:'/pages/goods/detail?goodsId='+id
				})
			},
			scrolltolower(e){//上拉刷新
			  if(this.page+1 <= this.allPage){
				  this.page = this.page+1
				  let data ={
					  groupId:this.groupId,
					  page: this.page,
					  pageSize: this.pageSize
				  }
				  this.getGoodsList(data)
			  }
			}
		},
		onShow() {
			this.initList()
		},
		// 点击 tab 时触发
		onTabItemTap(e){
			if(e.index==1){
				let t =setTimeout(()=>{
					this.clickGroup(0)
					clearTimeout(t)
				},50)
			}
		}
	}
</script>

<style lang="scss">
	.container {
		.left-box,.right-box{
			background-color: #fff;
			height: calc(100vh - 95px);
			.scroll-group,.scroll-goods{
				height: 100%;
			}
		}
		/* #ifdef MP-WEIXIN */
		.wx-left-box,.wx-right-box{
			height: 100vh;
		}
		/* #endif */
		.left-box{
			.scroll-group{
				font-size: 26upx;
				/deep/.uni-list-item__container{
					padding: 0;
					text-align: center;
					padding: 25upx 0;
					.uni-list-item__content{
						padding: 0;
					}
				}
				.active{
					/deep/.uni-list-item__container{
						background-color:$uni-border-2 !important;
						span{
							color: #009000 ;
						}
					}
				}
			}
		}
		.right-box{
			.scroll-goods{
				.item-box{
					display: flex;
					align-items: center;
					justify-content: space-between;
					border-bottom: 1px solid $uni-border-1;
					padding: 30upx;
					&:nth-last-child(2){
						border: 0;
					}
					.l{
						display: flex;
						align-items: flex-start;
						justify-content: flex-start;
                        .img{
							width: 150upx;
							height: 150upx;
							border-radius: 12upx;
						}
						.info{
							width: 250upx;
							height: 150upx;
							margin-left: 20upx;
							.name{
								font-size:30upx;
								font-weight: bold;
								color: $uni-main-color;
								margin-bottom: 6upx;
							}
							.des{
								font-size: 24upx;
								line-height: 34upx;
								height: 68upx;
								color: #B9B9B9;
							}
							.price-box{
								font-size: 26upx;
								color: #B9B9B9;
								line-height: 45upx;
								.price{
									font-size: 28upx;
									font-weight: bold;
									color: #ff557f;
									font-family: '雅黑';
								}
							}
						}
					}
					.r{
						width: 65upx;
						height:65upx;
						border-radius: 50%;
						border: 1px solid $uni-border-2;
						display: flex;
						align-items: center;
						justify-content: center;
						background-color:rgba(#F0F0F0, .5);
						.icon{
							width: 40upx;
							height: 40upx;
						}
					}
				}
			}
		}
	}
</style>